<template>
  <div class="page-layout users">
     <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div class="page-header">
      <h3><b>تعديل الحساب</b></h3>
    </div>
    <div class="text-center text-danger my-2" v-show="isLoading">
      <b-spinner class="align-middle"></b-spinner>
    </div>
    <b-form @submit.stop.prevent="onSubmit"  v-show="!isLoading">
      <b-container class="bv-example-row">
        <b-row>
          <b-col lg="6" md="12">
            <b-form-group id="group-student-edit-name" label="الاسم" label-for="student-edit-name">
              <b-form-input
                id="student-edit-name"
                name="student-edit-name"
                v-model="$v.form.name.$model"
                :state="validateState('name')"
                aria-describedby="input-edit-name"
              ></b-form-input>
              <b-form-invalid-feedback id="input-edit-name">
                الاسم مطلوب
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="6" md="12">
            <b-form-group id="group-student-edit-email" label="البريد الاكتروني" label-for="student-edit-email">
              <b-form-input
                id="student-edit-email"
                name="student-edit-email"
                v-model="$v.form.email.$model"
                :state="validateState('email')"
                aria-describedby="input-edit-email"
              ></b-form-input>
              <b-form-invalid-feedback id="input-edit-email" v-if='!$v.form.email.required'>
                البريد الالكتروني مطلوب
              </b-form-invalid-feedback>
              <b-form-invalid-feedback id="input-edit-email" v-if='!$v.form.email.email'>
                البريد الالكتروني غير صالح - xxx@yyy.zzz
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="6" md="12">
            <b-form-group id="group-student-edit-password" label="كلمة المرور" label-for="student-edit-password">
              <b-form-input
                id="student-edit-password"
                name="student-edit-password"
                v-model="$v.form.password.$model"
                :state="validateState('password')"
                aria-describedby="input-edit-password"
                type="password"
              ></b-form-input>
              <b-form-invalid-feedback id="input-edit-password" v-if='!$v.form.password.minLength'>
                كلمة المرور اكثر من 8 احرف
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
        </b-row>
        <b-button type="submit" variant="primary">تعديل</b-button>
      </b-container>
    </b-form>
  </div>
</template>

<script>
import { sendRequest }  from "../../../utils/RequestHelper.js"
import { validationMixin } from "vuelidate";
import store from "../../../store/index.js"
import { required, email} from "vuelidate/lib/validators";

export default {
  mixins: [validationMixin],
  data() {
    return {
      alertCountdown: 0,
      alertType: '',
      alertText: '',
      form: {
        name: null,
        email: null,
        password: null
      },
      adminMail: null
    };
  },
  validations: {
    form: {
      name: {
        required
      },
      email: {
        required,
        email
      },
      password: {
      }
    }
  },
  methods: {
    validateState(name) {
      const { $dirty, $error } = this.$v.form[name];
      return $dirty ? !$error : null;
    },
    countDownChanged(alertCountdown) {
      this.alertCountdown = alertCountdown
    },
    onSubmit() {
      this.$v.form.$touch();
      if (this.$v.form.$anyError) {
        return;
      }
      this.isLoading = true;
      this.saveAdminData();
    },
    saveAdminData() {
      this.isLoading = true;
      if(this.form.password == null) 
        delete this.form.password
      if(this.form.email == this.adminMail) 
        delete this.form.email
      this.isLoading = false;
      store.commit("SET_USERNAME", this.form.name);
      this.alertType = "success"
      this.alertText = "تم تعديل الحساب بنجاح"
      this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
      this.loadData();
      // var successCallback = () => {
      //   this.isLoading = false;
      //   store.commit("SET_USERNAME", this.form.name);
      //   this.alertType = "success"
      //   this.alertText = "تم تعديل الحساب بنجاح"
      //   this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
      //   this.loadData();
      // };
      // var errorCallback = () => {
      //   this.isLoading = false;
      //   this.alertType = "danger"
      //   this.alertText = "حدث خطأ ما ، حاول قريبًا"
      //   this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
      // };
      // sendRequest('patch', '/admins/update', true, this.form, successCallback, errorCallback);
    },
    loadData() {
      this.isLoading = true;
      this.form = {
        id: 1,
        first_name: "admin",
        last_name: "1",
        email: "admin@i.com",
      };
      delete this.form.password;
      this.adminMail = this.form.email;
      this.isLoading = false;
      // var successCallback = (response) => {
      //   this.form = response.data;
      //   delete this.form.password;
      //   this.adminMail = this.form.email;
      //   this.isLoading = false;
      // };
      // var errorCallback = () => {
      //   this.isLoading = false;
      //   this.alertType = "danger"
      //   this.alertText = "حدث خطأ ما ، حاول قريبًا"
      //   this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
      // };
      // sendRequest('get', '/admins/profile', true, null, successCallback, errorCallback);
    }
  },
  created() {
    this.isLoading = true;
    this.loadData();
  }
};
</script>