<template>
  <div class=" pageContent">
     <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div class="page-header">
      <div class="row"  v-show="!pageIsLoading">
        <div class="col-md-3 col-sm-12 col-xs-12 fiter-col">
          <label>{{langIsEng ? 'Phone' : 'المحمول'}}</label>
          <b-form-input :placeholder="langIsEng ? 'Phone number' : 'رقم المحمول'" v-model="selectedPhone" @change="loadData"
          ></b-form-input>  
        </div>
        <div class="col-md-3 col-sm-12 col-xs-12 fiter-col">
          <label>{{langIsEng ? 'Doctor' : 'الطبيب'}}</label>
          <b-form-select class="grade-selectOption" @change="loadData" v-model="selectedDoctor" :options="doctors"></b-form-select>
        </div>
        <div class="col-md-3 col-sm-12 col-xs-12 fiter-col">
          <label>{{langIsEng ? 'Visit status' : 'حالة الزيارة'}}</label>
          <b-form-select class="grade-selectOption" @change="loadData" v-model="selectedVisitStatus" :options="langIsEng ? visitsStatusEn : visitsStatusAr"></b-form-select>
        </div>
        <div class="col-md-3 col-sm-12 col-xs-12 fiter-col">
          <label for="example-datepicker">{{langIsEng ? 'Date' : 'التاريخ'}}</label>
          <b-form-datepicker id="example-datepicker" :placeholder="langIsEng ? 'Data' : 'التاريخ'" @input="loadData" v-model="selectedDate" today-button reset-button close-button class="mb-2"></b-form-datepicker>
        </div>
      </div>
    </div>
    <div class="text-center text-danger my-2" v-show="pageIsLoading">
      <b-spinner class="align-middle"></b-spinner>
    </div>
    <div class="row"  v-show="!pageIsLoading">
      <div class="col-md-12">
        <div class="">
          <div class="">
            <div class="table-responsive">
              <table class="table table-bordered table-hover">
                <thead>
                  <tr>
                    <th width="5%">
                      <span class="tbl-data">#</span>
                    </th>
                    <th>
                      <div class="tbl-data">{{langIsEng ? "Name" : "الاسم"}}</div>
                    </th>
                    <th>
                      <span class="tbl-data">{{langIsEng ? "Phone" : "المحمول"}}</span>
                    </th>
                    <th>
                      <span class="tbl-data">{{langIsEng ? "Doctor" : "الطبيب"}}</span>
                    </th>
                    <th>
                      <span class="tbl-data">{{langIsEng ? "Date and time" : "التاريخ والوقت"}}</span>
                    </th>
                    <th style="z-index:1">
                      <span class="tbl-data">{{langIsEng ? "Statue" : "الحالة"}}</span>
                    </th>
                    <th style="z-index:1">
                      <span class="tbl-data"></span>
                    </th>
                    <th style="z-index:1" width="10%" class="td-option">
                      <span clas="tbl-data">	
                        <b-button @click="goToRoute('AddVisit')" size="sm" class="btn btn-success add-admin-btn options-table-btn" data-toggle="modal" data-target="#addAdmin" ><i class="fa fa-plus " :class="{'ml-2': !langIsEng, 'mr-2': langIsEng}"></i>
                          {{langIsEng ? "New Visit" : "إضافة زيارة"}}
                        </b-button>	
                      </span>
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr class="no-data-row" v-if="visits.length == 0 && isLoading">
                    <td colspan="7">
                      <div class="text-center >text-danger my-2">
                        <b-spinner class="align-middle"></b-spinner>
                      </div>
                    </td>
                  </tr>
                  <tr class="no-data-row" v-if="visits.length == 0 && !isLoading">
                    <td colspan="8">
                      لا يوجد معلومات
                    </td>
                  </tr>
                  <tr v-for="(item, index) in visits" :key="index">
                    <td>
                      {{++index}}
                    </td>
                    <td>
                      <div class="tbl-data">{{item.patient ? item.patient.name : ''}}</div>
                    </td>
                    <td>
                      <span class="tbl-data">{{item.patient ? item.patient.phone : ''}}</span>
                    </td>
                    <td>
                      <span class="tbl-data">{{(langIsEng ? "Dr/ ":"د/ ") + doctorsDic[item.doctor_id].name}}</span>
                    </td>
                    <td>
                      <span class="tbl-data">{{item.date + " - " + item.time}}</span>
                    </td>
                    <td>
                      <span class="tbl-data"><b-badge :variant="item.status=='منتظر'?'warning':item.status=='مع الطبيب'?'primary':item.status=='جاهز للدفع'?'danger':'info'">{{langIsEng ? statusDicEn[item.status] : statusDicAr[item.status]}}</b-badge></span>
                    </td>
                    <td class="td-option">
                      <div class="form-group row-option">
                        <b-button v-if="item.status=='منتظر'" @click="confirmupdateVisit(item.id, 'مع الطبيب')" type="button" size="lg" class="btn btn-success options-table-btn edit-options-table-btn">{{langIsEng ? "Go to doctor" : "ادخل للطبيب"}}</b-button>
                        <b-button style="cursor:no-drop" v-if="item.status=='جاهز للدفع'" disabled type="button" size="lg" class="btn btn-success options-table-btn edit-options-table-btn">{{langIsEng ? "Cash in" : "دفع المبلغ"}}</b-button>
                      </div>
                    </td>
                    <td class="td-option">
                      <div class="form-group row-option">
                        <b-button type="button" size="sm" class="btn btn-primary options-table-btn edit-options-table-btn" @click="goToEditRoute(item.id, item.patient.name)" ><i class="fa fa-edit"></i></b-button>
                        <b-button type="button" size="sm" class="btn btn-danger options-table-btn del-options-table-btn" data-toggle="modal" data-target="#delAdmin" @click="openDeleteItem(item.id);" ><i class="fa fa-trash"></i></b-button>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <b-pagination v-model="currentPage" :total-rows="total" :per-page="10" align="center" size="sm"></b-pagination>
          </div>
        </div>
      </div>
    </div>

    <b-modal v-model="confirmUpdate" centered title="تعديل الزيارة">
      <p class="my-4">{{langIsEng ? 'Are you sure?' : ' موافق على تعديل الزيارة الى "' + currentStatus + '" ؟'}}</p>
      <template v-slot:modal-footer>
        <div class="w-100">
          <b-button variant="primary" @click="updateVisitStatus()" size="sm" class="float-left mr-1">
            موافق
          </b-button>
          <b-button variant="secondary" size="sm" class="float-left" @click="confirmUpdate=false">
            الغاء
          </b-button>
        </div>
      </template>
    </b-modal>



    <b-modal v-model="delModal" centered :title="langIsEng ? 'Remove visit' : 'حذف الزيارة'">
      <p class="my-4">{{langIsEng ? 'Do you want to remove this visit?' : 'هل تريد حذف هذه الزيارة؟'}}</p>
      <template v-slot:modal-footer>
        <div class="w-100">
          <b-button variant="primary" size="sm" :class="langIsEng ? 'float-right ml-1' : 'float-left mr-1'" @click="deleteItem()">
            {{langIsEng ? 'Remove' : 'حذف'}}
          </b-button>
          <b-button variant="secondary" size="sm" :class="langIsEng ? 'float-right' : 'float-left'" @click="delModal=false">
            {{langIsEng ? 'Cancel' : 'الغاء'}}
          </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>
<script>
  import { sendRequest }  from "../../../utils/RequestHelper.js"
  import store from "../../../store/index.js"

  export default {
    name: 'Home',
    data() {
      return {
        visits:[],
        isLoading: true,
        pageIsLoading: false,
        alertType: '',
        alertText: '',
        alertCountdown: 0,
        showDismissibleAlert: false,
        currentID: null,
        currentStatus: null,
        delModal: false,
        currentPage: 1,
        total: null,
        from: 1,
        lang: null,
        langIsEng: null,
        statusDicAr: {
          'منتظر': "منتظر",
          'مع الطبيب': "مع الطبيب",
          'جاهز للدفع': "جاهز للدفع",
          'انتهت الزياره': "انتهت الزيارة"

        },
        statusDicEn: {
          'منتظر': "Waiting",
          'مع الطبيب': "With doctor",
          'جاهز للدفع': "Ready to pay",
          'انتهت الزياره': "Finished"

        },
        selectedDate: "",
        selectedVisitStatus: null,
        visitsStatusAr: [
           {
            text: "عرض الكل",
            value: null
          },
          {
            text: "منتظر",
            value: "منتظر"
          },
          {
            text: "مع الطبيب",
            value: "مع الطبيب"
          },
          {
            text: "جاهز للدفع",
            value: "جاهز للدفع"
          },
          {
            text: "انتهت الزيارة",
            value: "انتهت الزياره"
          }
        ],
        visitsStatusEn: [
          {
            text: "Show all",
            value: null
          },
          {
            text: "Waiting",
            value: "منتظر"
          },
          {
            text: "With doctor",
            value: "مع الطبيب"
          },
          {
            text: "Ready to pay",
            value: "جاهز للدفع"
          },
          {
            text: "Finished",
            value: "انتهت الزياره"
          }
        ],


        selectedDoctor: null,
        selectedPhone: null,
        doctors: [],
        doctorsDic: {},

        confirmUpdate: false


        
      }
    },
    watch: {
      currentPage: {
        handler: function(value) {
          this.loadData(value);
        }
      }
    },
    methods: {
      goToRoute(routeName) {	
        this.$router.push({	
          name: routeName	
        })
      },
      getDoctors(){
        this.pageIsLoading = true;
        this.isLoading = true;
        this.doctors = [];
        this.doctorsDic = {};
        var successCallback = (response) => {
          let text = '';
          if(this.langIsEng)
            text = "All doctors"
          else
            text = 'كل الاطباء'
          this.doctors.push({
            text: text,
            value: null
          })
          let doctors = response.data.data;
          doctors.forEach(doctor => {
            doctor.text = doctor.name;
            doctor.value = doctor.id;
            this.doctorsDic[doctor.id] = doctor;
            this.doctors.push(doctor)
          });
          this.loadData();
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = "حدث خطأ ما ، حاول قريبًا"
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/nurse/doctors', true, null, successCallback, errorCallback);
      },
      loadData(){
        this.pageIsLoading = true;
        this.isLoading = true;

        // status filter
        let status = ""
        if(this.selectedVisitStatus != null) {
          status = "&status=" + this.selectedVisitStatus;
        }

        // doctor filter
        let doctor = ""
        if(this.selectedDoctor != null) {
          doctor = "&doctor_id=" + this.selectedDoctor;
        }

        // phone filter
        let phone = ""
        if(this.selectedPhone != null) {
          phone = "&phone=" + this.selectedPhone;
        }

        // date filter
        let date = ""
        if(this.selectedDate != null && this.selectedDate != '') {
          date = "&date=" + this.selectedDate;
        }
        this.visits = []
        var successCallback = (response) => {
          this.visits = response.data.data;
          this.total = response.data.total;
          this.from = response.data.from;
          this.isLoading = false;
          this.pageIsLoading = false;
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.pageIsLoading = false;
          this.alertType = "danger"
          this.alertText = "حدث خطأ ما ، حاول قريبًا"
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/nurse/visits?' + status + phone + doctor + date, true, null, successCallback, errorCallback);
      },
      openDeleteItem(id){
        if(id) {
          this.delModal = true;
          this.currentID = id;
        }
      },
      confirmupdateVisit(id, status) {
        this.currentID = id;
        this.currentStatus = status;
        this.confirmUpdate = true;
      },
      updateVisitStatus() {
        this.confirmUpdate = false;
        this.pageIsLoading = true;
        this.isLoading = true;
        var successCallback = () => {
          this.alertType = "success"
          this.alertText = this.langIsEng ? "Patient go to doctor." : "تم تحديث الزيارة - للذهاب للطبيب."
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          this.loadData();
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.alertType = "danger"
          this.alertText = this.langIsEng ? "Wrong .. try soon !" : "حدث خطأ ما ، حاول قريبًا"
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('put', '/nurse/visits/' + this.currentID, true, {'status': this.currentStatus}, successCallback, errorCallback);
      },
      deleteItem() {
        this.pageIsLoading = true;
        this.delModal = false;
        this.isLoading = true;
        if(this.currentID) {
          this.isLoading = true;
          var successCallback = () => {
            this.alertType = "success"
            this.alertText = this.langIsEng ? "Visit removed successfully." : "تم حذف الزيارة."
            this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
            this.loadData();
          };
          var errorCallback = () => {
            this.isLoading = false;
            this.alertType = "danger"
            this.alertText = this.langIsEng ? "Wrong .. try soon !" : "حدث خطأ ما ، حاول قريبًا"
            this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          };
          sendRequest('delete', '/nurse/visits/' + this.currentID, true, null, successCallback, errorCallback);
        }
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      goToEditRoute(id, name){
        if(id) {
          this.$router.push({
            name: 'UpdateVisit',
            params: {
              id,
              name
            }
          })
        }
      }
    },
    created() {
      this.getDoctors(this.currentPage);
      this.lang = store.getters.getlang;
      this.langIsEng = store.getters.getlang == "en";
    },
  }
</script>
<style scoped>
  @import '../../../assets/css/tableContent.css';
</style>